body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*Не забывать побить стили*/
html, body, #root {
  min-height: 100vh;
}
.paper {
  background-color: #fff;
  box-shadow: 0 2px  10px 2px rgba(0, 0, 0, .1);
}
.page {
  color: rgba(0, 0, 0, .7);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, .1);
}

.header {
  z-index: 1;
  overflow: hidden;
  font-family: arial;

}

.logo {

  font-style: italic;
  font-size: 20px;
  font-weight: 700;
}

.white {
  text-decoration: none;
  color: white;
}

.medium {
  font-size: 20px;
}

.big {
  font-size: 30px;
  font-weight: 700;
}

.small {
  font-size: 14px;
}

.content {
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
}

.center {
  height: 60px;
  margin: 0 auto;
  padding: 0px 20px 0px 20px;
  max-width: 1280px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.footer {
  -webkit-flex: 0 0 auto;
          flex: 0 0 auto;
}
.card {
  padding: 20px 30px;
  margin: 30px;
  border-radius: 25px;
  overflow: hidden;
}
.nav {
  left: -300px;
  padding-top: 60px;
  position: absolute;
  width: 300px;
  min-height: 100vh;
  transition: all 500ms;
}
.nav.visible {
  left: 0;
}

.nav__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.nav__link:hover {
  background-color: rgba(0, 0, 0, .04);
}

.nav__link:active {
  background-color: rgba(0, 0, 0, .07);
}

.nav__link {
  padding: 20px 0;
  border-bottom: 2px solid rgba(0, 0, 0, .7);
  color: rgba(0, 0, 0, .7);
  text-decoration: none;
  text-align: center;
  display: block;
  font-size: 30px;
}
.hamburger {
  cursor: pointer;
}

.hamburger:hover {
  background-color: rgba(0, 0, 0, .04);
}

.hamburger:active {
  background-color: rgba(0, 0, 0, .07);
}
.mail {
  color: rgba(0, 0, 0, .7);
  padding-left: 5px;

}
@media (min-width: 1280px) {
  .content {
    -webkit-flex: 1 0 auto;
            flex: 1 0 auto;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-flex-direction: row;
            flex-direction: row;
  }
  .card {
    width: 500px;
    margin: 10px;
  }
}

